.map {
  position: relative;
}
.map.container {
  padding-inline: 0;
}

.marquee-wrap {
  background: #9B6D44;
  padding: clamp(0.625rem, -0.625rem + 1.5625vw, 0.9375rem) 0;
  position: relative;
  display: flex;
  overflow: hidden;
  gap: 42px;
}

.marquee-row {
  flex-shrink: 0;
  display: flex;
  gap: 42px;
  justify-content: space-around;
  min-width: 100%;
  animation: scroll 15s linear infinite;
}

.marquee-row-item {
  text-transform: uppercase;
  font-size: clamp(1.125rem, 1.0041322314rem + 0.4958677686vw, 1.5rem);
  font-weight: 500;
  line-height: 1.2;
  -webkit-text-stroke: 0.3px white;
  color: var(--color-white);
  position: relative;
  text-wrap: nowrap;
}

.marquee-row-item::before {
  content: attr(data-name);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  color: rgb(255, 1, 97);
  -webkit-text-stroke: 1px transparent;
  overflow: hidden;
  transition: width 0.5s ease;
}

.marquee-row-item:hover::before {
  width: 100%;
}

@keyframes scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-100% - 42px));
  }
}
.footer {
  background-color: var(--color-black);
  padding: clamp(0.9375rem, 0.6353305785rem + 1.2396694215vw, 1.875rem) 0;
  font-size: clamp(1rem, 0rem + 1.25vw, 1.25rem);
}
